import * as React from 'react';
import { VPage, TabCaptionComponent, Page } from 'tonva';
import { CApp } from './CApp';

const color = (selected: boolean) => selected === true ? 'text-primary' : 'text-muted';

export class VMain extends VPage<CApp> {
    async open(param?: any) {
        this.openPage(this.render);
    }

    render = (param?: any): JSX.Element => {
        let { cGroup, cJob: cTodo, cHome, cMe/*, cPosts, cMedia, cTemplets*/ } = this.controller;
        let faceTabs = [
			{ name: 'note', label: '', icon: 'home', content: cGroup.tab, },
			{ name: 'todo', label: '', icon: 'list', content: cTodo.tab, onShown: cTodo.load },
            { name: 'home', label: '', icon: 'tasks', content: cHome.tab, },
            { name: 'me', label: '', icon: 'user', content: cMe.tab }
        ].map(v => {
            let { name, label, icon, content, onShown/*, notify, onShown*/ } = v;
            return {
                name: name,
                caption: (selected: boolean) => TabCaptionComponent(label, icon, color(selected)),
                content: content,
                onShown: onShown,
            }
		});
		return <Page tabsProps={{tabs:faceTabs}} />;
		/*
        return <Page header={false} headerClassName={"bg-info"} >
            <Tabs tabs={faceTabs} />
        </Page>;
		//
		*/
    }
}
