import * as React from 'react';
import { Page, VPage, Form, ItemSchema, UiSchema, UiTextItem, UiButton, NumSchema, UiTextAreaItem } from 'tonva';
import { CHome } from './CHome';

export class VTest extends VPage<CHome> {
    async open(param?: any) {
        this.openPage(this.page);
    }

    private page = () => {
        let items:ItemSchema[] = [
            {name: 'name1', type: 'string', required: true},
            {name: 'name2', type: 'string'},
            {name: 'num', type: 'number', min: 10, max: 50} as NumSchema,
            {name:'submit', type:'button'}
        ];
        let uis: UiSchema = {
            items: {
                name1: {widget: 'text', label: '', placeholder: ''} as UiTextItem,
                name2: {widget: 'textarea', label: '2', placeholder: '2'} as UiTextAreaItem,
                submit: {widget: "button", label: '', className: 'btn btn-success'} as UiButton
            }
        }
 
        return <Page header="">
            <Form className="m-3"
                schema={items}
                uiSchema={uis} fieldLabelSize={3} />
        </Page>
        //return <ImageUploader size="lg" />
    }
}
